@import "normalize.css";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-family: "Inter", sans-serif;
}

body {
  font-size: 16px;
  font-weight: 500;
  color: #A6A6A6;
}

.container {
  width: 1180px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #A6A6A6;
}

ul {
  list-style: none;
}

.header_row {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
}
.header_row .header_logo {
  font-size: 50px;
  font-weight: 600;
  color: #54BD95;
}